<template>
  <div class="notifications">
    <h1>Notifications</h1>
    <hr/><br/>
    <notification-box
      v-for="notification in myNotifications" :key="notification._id"
      :color="getColor(notification.importance)"
      :text="notification.message"
      :id="notification._id"
    />
  </div>
</template>

<script>
import NotificationBox from '@/components/NotificationBox.vue'

export default {
  name: 'Notifications',
  components: { NotificationBox },
  computed: {
    myNotifications () {
      return this.$store.getters.getUserNotifications
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  mounted () {
    // this.$store.dispatch('loadProjects')
    this.$store.dispatch('loadUserNotifications', this.$store.state.auth.user)
    // this.$store.dispatch('loadPhotographers')
  },
  methods: {
    getColor (value) {
      switch (value) {
        case '3':
          return 'primary'
        case '2':
          return 'warning'
        case '1':
          return 'error'
        default:
          break
      }
    }
  }
}
</script>

<style scoped>

.notifications {
  padding: 40px;
  padding-left : 100px;
  padding-right : 100px;
}

</style>
