import ProjectService from '../services/project.service'

export const project = {
  // namespaced: true,
  // state: initialState,
  state: {
    projects: [],
    userProjects: []
  },
  actions: {
    loadProjects ({ commit }) {
      ProjectService.getProjects().then(
        response => {
          commit('setProjects', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    loadUserProjects ({ commit }, item) {
      ProjectService.getProjectByPhotographer(item).then(
        response => {
          commit('setUserProjects', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    updateProject ({ dispatch }, project) {
      ProjectService.putProjects(project).then(
        response => {
          dispatch('loadProjects')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    deleteProject ({ dispatch }, project) {
      ProjectService.deleteProject(project).then(
        response => {
          dispatch('loadProjects')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    validateProject ({ dispatch }, project) {
      ProjectService.validateProject(project).then(
        response => {
          dispatch('loadProjects')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    addProject ({ dispatch }, project) {
      ProjectService.postProject(project).then(
        response => {
          dispatch('loadProjects')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    setPhotographerToProject ({ dispatch }, projectId, photographerId) {
      ProjectService.setPhotographer(projectId, photographerId).then(
        response => {
          dispatch('loadProjects')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    }
  },
  mutations: {
    setProjects (state, projects) {
      state.projects = projects
    },
    setUserProjects (state, projects) {
      state.userProjects = projects
    }
  },
  getters: {
    getProjects (state) {
      return state.projects
    },
    getUserProjects (state) {
      return state.userProjects
    },
    getProject: (state) => (id) => {
      return state.projects.find(project => project._id === id)
    }
  }
}
