import axios from 'axios'
import authHeader from './auth-header'

const API_URL = 'https://app.afroto.com/api/photographer/'

class UserService {
  getPhotographers () {
    return axios.get(API_URL, { headers: authHeader() })
  }

  getPhotographer (item) {
    return axios.get(API_URL + item._id, { headers: authHeader() })
  }

  addEquipement (data) {
    return axios.post(API_URL + 'equipement/' + data._id, {
      equipement: data.equipement,
      type: data.type
    }, { headers: authHeader() })
  }

  deleteEquipement (data) {
    return axios.delete(API_URL + 'equipement/' + data._id + '/' + data.type + '/' + data.camera, { headers: authHeader() })
  }

  deletePhotographer (data) {
    return axios.delete(API_URL + data._id, { headers: authHeader() })
  }

  updatePhotographer (data) {
    return axios.put(API_URL + data._id, {
      phone: data.phone,
      name: data.name,
      email: data.email,
      birthdate: data.birthdate,
      city: data.city,
      facebook: data.facebook,
      instagram: data.instagram
    }, { headers: authHeader() })
  }
}

export default new UserService()
