<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" md="8">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Mon Profil</v-toolbar-title>
          </v-toolbar>
          <v-tabs grow>
            <v-tab>Informations Gnrales</v-tab>
            <v-tab>Equipement</v-tab>
            <v-tab-item>
              <v-card-text>
                <v-row align="center" justify="center">
                  <v-col cols="6">
                    <div>Appareils :</div>
                    <v-chip
                      class="ma-2"
                      v-for="camera in this.user.cameras"
                      :key="camera"
                      text-color="white"
                      color="blue"
                      close
                      @click:close="deleteEquipement('camera', camera)"
                    >
                      {{ camera }}
                    </v-chip>
                    <v-chip
                      v-if="this.user.cameras && this.user.cameras.length === 0"
                      class="ma-2"
                      >Aucun appareil</v-chip
                    >
                    <v-dialog v-model="dialog" width="300">
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" icon v-bind="attrs" v-on="on">
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <v-form @submit.prevent="addEquipement('camera')">
                        <v-card>
                          <v-card-title class="headline">
                            Ajouter un appareil
                          </v-card-title>

                          <v-card-text>
                            <v-text-field
                              v-model="camera"
                              label="Modle de l'appareil"
                            ></v-text-field>
                          </v-card-text>

                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn
                              type="submit"
                              color="primary"
                              text
                              @click="dialog = false"
                            >
                              Valider
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-form>
                    </v-dialog>
                  </v-col>
                </v-row>
                <v-row align="center" justify="center">
                  <v-col cols="6">
                    <div>Objectifs :</div>
                    <v-chip
                      class="ma-2"
                      v-for="lens in this.user.lenses"
                      :key="lens"
                      text-color="white"
                      color="blue"
                      close
                      @click:close="deleteEquipement('lens', lens)"
                    >
                      {{ lens }}
                    </v-chip>
                    <v-chip
                      v-if="this.user.lenses && this.user.lenses.length === 0"
                      class="ma-2"
                      >Aucun objectifs</v-chip
                    >
                    <v-dialog v-model="dialog2" width="300">
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" icon v-bind="attrs" v-on="on">
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <v-form @submit.prevent="addEquipement('lens')">
                        <v-card>
                          <v-card-title class="headline">
                            Ajouter un appareil
                          </v-card-title>

                          <v-card-text>
                            <v-text-field
                              v-model="lens"
                              label="Modle de l'appareil"
                            ></v-text-field>
                          </v-card-text>

                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn
                              type="submit"
                              color="primary"
                              text
                              @click="dialog2 = false"
                            >
                              Valider
                            </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-form>
                    </v-dialog>
                  </v-col>
                </v-row>
                <v-row align="center" justify="center">
                  <v-col cols="6">
                    <div>Types de photo :</div>
                    <v-chip
                      class="ma-2"
                      v-for="badge in this.user.badges"
                      :key="badge"
                      text-color="white"
                      color="blue"
                      close
                      @click:close="removeBadge(badge)"
                    >
                      {{ badge }}
                    </v-chip>
                    <v-chip
                      v-if="this.user.cameras && this.user.badges.length === 0"
                      class="ma-2"
                      >Aucun types</v-chip
                    >
                  </v-col>
                </v-row>
                <v-row> </v-row>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <fields-updater></fields-updater>
              </v-card-actions>
            </v-tab-item>
            <v-tab-item>
              <v-form @submit.prevent="updateItem">
                <v-card-text>
                  <v-row>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.name"
                        label="Nom et Prnom"
                        name="login"
                        prepend-icon="mdi-account"
                        type="text"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.email"
                        label="Email"
                        name="login"
                        prepend-icon="mdi-email"
                        type="email"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.birthdate"
                        label="Date de naissance"
                        prepend-icon="mdi-calendar"
                        type="date"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.phone"
                        label="Tlphone"
                        prepend-icon="mdi-phone"
                        type="text"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.city"
                        label="Ville"
                        prepend-icon="mdi-home"
                        type="text"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.facebook"
                        label="Facebook"
                        prepend-icon="mdi-facebook"
                        type="text"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="6" sm="12">
                      <v-text-field
                        v-model="user.instagram"
                        label="Instagram"
                        prepend-icon="mdi-instagram"
                        type="text"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn type="submit" color="primary"
                    >Mettre  jour mes donnes</v-btn
                  >
                </v-card-actions>
              </v-form>
            </v-tab-item>
          </v-tabs>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import User from '../../models/user'
import FieldsUpdater from '../../components/FieldsUpdater.vue'
// import Vue from 'vue'

export default {
  components: { FieldsUpdater },
  name: 'Profile',
  data () {
    return {
      user: new User(),
      dialog: false,
      dialog2: false,
      dialog3: false,
      items: [
        'Portrait',
        'Evenementiel',
        'Mode',
        'Nu',
        'Animalire',
        'Nature morte',
        'Culinaire',
        'Arienne',
        'Photojournalisme',
        'Mariage',
        "Oeuvres d'Art"
      ],
      value: [],
      camera: '',
      lens: ''
    }
  },
  computed: {
    currentUser () {
      return this.$store.state.auth.user
    },
    isAdmin () {
      if (!this.loggedIn) {
        return false
      }
      return this.currentUser.roles.includes('ROLE_ADMIN')
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    },
    getCameraBadges () {
      if (!this.user.badges) return ['']
      this.user.badges.forEach((badge) => {
        if (badge.type === 'camera') {
          return badge.name
        }
      })
      return null
    },
    getLensesBadges () {
      return null
    },
    getTypesBadges () {
      return null
    }
  },
  created () {
    if (!this.currentUser) {
      this.$router.push('/login')
    }
    this.$store.dispatch('loadPhotographers')
  },
  mounted () {
    this.setUser()
  },
  methods: {
    setUser () {
      const userId = this.$route.params.id ?? this.$store.state.auth.user.id
      this.user = this.$store.getters.getPhotographer(userId) ?? {}
      this.user.birthdate = this.formatDate(new Date(this.user.birthdate))
    },
    formatDate (date) {
      return date.toISOString().substr(0, 10)
    },
    updateItem () {
      this.user._id = this.$route.params.id ?? this.$store.state.auth.user.id
      this.$store.dispatch('updateProfile', this.user).then(
        (response) => {
          this.$store.dispatch('loadPhotographers')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    },
    addEquipement (type) {
      if (!this.camera && type === 'camera') return
      if (!this.lens && type === 'lens') return
      const id = this.$route.params.id ?? this.$store.state.auth.user.id
      this.$store
        .dispatch('addEquipement', {
          _id: id,
          equipement: type === 'camera' ? this.camera : this.lens,
          type: type
        })
        .then(
          (response) => {
            this.user.cameras = response.data.data.cameras
            this.user.lenses = response.data.data.lenses
          },
          (error) => {
            alert(error.response.data.message)
          }
        )
      this.camera = ''
      this.lens = ''
    },
    deleteEquipement (type, camera) {
      const id = this.$route.params.id ?? this.$store.state.auth.user.id
      this.$store
        .dispatch('deleteEquipement', { _id: id, camera: camera, type: type })
        .then(
          (response) => {
            if (type === 'camera') {
              this.user.cameras = response.data.data.cameras
            } else {
              this.user.lenses = response.data.data.lenses
            }
          },
          (error) => {
            alert(error.response.data.message)
          }
        )
    }
  }
}
</script>

<style scoped>
</style>
