<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="8">
        <v-form @submit.prevent="handleForm">
          <v-card class="elevation-12">
            <v-toolbar color="primary" dark flat>
              <v-toolbar-title>Ajouter un projet</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-row>
                <v-col cols="12"><h1>Gnral</h1></v-col>
              </v-row>
              <v-row>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Nom du projet"
                    name="name"
                    v-model="project.name"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Type de projet"
                    name="type"
                    v-model="project.type"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Client"
                    name="client"
                    v-model="project.company"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-menu
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field
                        :value="dateRangeText"
                        label="Calendrier"
                        prepend-icon="mdi-calendar"
                        v-bind="attrs"
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      v-model="project.dates"
                      range
                      name="dates"
                      @input="menu2 = false"
                    ></v-date-picker>
                  </v-menu>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-select
                    :items="itemSelect"
                    menu-props="auto"

                    label="Select"
                    chips
                    name="status"
                    v-model="project.status"
                    prepend-icon="mdi-account"
                    single-line
                  >
                    <template #selection="{ test }">
                      <v-chip
                        text-color="white"
                        close
                        :color="getColor(test)"
                        >{{ test }}</v-chip
                      >
                    </template>
                  </v-select>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Rmunration"
                    name="budget"
                    v-model="project.remuneration"
                    prepend-icon="mdi-bitcoin"
                    type="text"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12"><h1>Contact</h1></v-col>
              </v-row>
              <v-row>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Email"
                    name="email"
                    v-model="project.email"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Tlphone"
                    name="phone"
                    v-model="project.phone"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Adresse"
                    name="address"
                    v-model="project.address"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Pays"
                    name="country"
                    v-model="project.country"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12"><h1>Contact</h1></v-col>
              </v-row>
              <v-row>
                <v-col cols="6" md="6" sm="12">
                  <v-textarea
                    prepend-icon="mdi-account"
                    name="comments"
                    v-model="project.comments"
                    label="Comments"
                  ></v-textarea>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-textarea
                    prepend-icon="mdi-account"
                    name="informations"
                    v-model="project.informations"
                    label="Informations"
                  ></v-textarea>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12"><h1>Rendu</h1></v-col>
              </v-row>
              <v-row>
                <v-col cols="12" md="12" sm="12">
                  <v-text-field
                    disabled
                    label="URL Photo"
                    name="pictures"
                    v-model="project.pictures"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
            type=submit
            color="primary">Valider</v-btn>
          </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Project from '../../models/project'

export default {
  name: 'Projects',
  data () {
    return {
      loading: false,
      options: {},
      itemSelect: ['En cours', 'Fini', 'En pause', 'Annul'],
      dates: [],
      project: new Project(),
      menu2: false
    }
  },
  computed: {
    dateRangeText () {
      return this.dates.join(' ~ ')
    },
    isAdmin () {
      if (!this.loggedIn) {
        return false
      }
      return this.currentUser.roles.includes('ROLE_ADMIN')
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  methods: {
    handleForm () {
      this.$store.dispatch('addProject', this.project).then(
        (response) => {
          this.$router.push('/')
        },
        (error) => {
          this.loading = false
          this.message =
                error.response.data.message
        }
      )
    },
    redirect: function () {
      this.$router.push('/')
    },
    getColor: function (hello) {
      if (hello === 'En cours') {
        return 'blue'
      } else if (hello === 'Fini') {
        return 'red'
      } else if (hello === 'En pause') {
        return 'orange'
      } else if (hello === 'Annul') {
        return 'black'
      }
    }
  }
}
</script>

