            </v-toolbar>
            <v-card-text>
              <v-row>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Nom d'utilisateur"
                    name="username"
                    v-model="user.username"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
