                    v-model="user.birthdate"
                    prepend-icon="mdi-calendar"
                    type="date"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Tlphone"
                    name="phone"
                    v-model="user.phone"
                    prepend-icon="mdi-phone"
                    type="text"
                  ></v-text-field>
                </v-col>
                <v-col cols="6" md="6" sm="12">
                  <v-text-field
                    label="Ville"
