<script>
import User from '../../models/user'
// import BigButton from '../components/BigButton.vue'
export default {
  name: 'Register',
  components: {},
  data () {
    return {
      user: new User(),
      showError: false
    }
  },
  methods: {
    handleRegister () {
      this.$store.dispatch('auth/register', this.user).then(
        (response) => {
          this.$router.push('/login')
        },
        (error) => {
          this.loading = false
          this.message = error.response.data.message
        }
      )
    }
  }
}
</script>

