import NotificationService from '../services/notification.service'

export const notification = {
  // namespaced: true,
  // state: initialState,
  state: {
    notifications: [],
    userNotifications: []
  },
  actions: {
    loadNotifications ({ commit }) {
      NotificationService.getNotifications().then(
        response => {
          commit('setNotifications', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    loadUserNotifications ({ commit }, item) {
      NotificationService.getNotificationByPhotographer(item).then(
        response => {
          commit('setUserNotifications', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    updateNotification ({ dispatch }, notification) {
      NotificationService.putNotifications(notification).then(
        response => {
          dispatch('loadNotifications')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    deleteNotification ({ dispatch }, notification) {
      NotificationService.deleteNotification(notification).then(
        response => {
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    addNotification ({ dispatch }, notification) {
      NotificationService.postNotification(notification).then(
        response => {
          dispatch('loadNotifications')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    }
  },
  mutations: {
    setNotifications (state, notifications) {
      state.notifications = notifications
    },
    setUserNotifications (state, notifications) {
      state.userNotifications = notifications
    }
  },
  getters: {
    getNotifications (state) {
      return state.notifications
    },
    getUserNotifications (state) {
      return state.userNotifications
    }
  }
}
