<template>

    <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="9">
          <h1>Demandes en cours</h1>
          <hr/><br/>
          <v-data-table
          :headers="headers"
          :items="openRequests"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            color="green"
            class="mr-2"
            @click="setState(item, 'accepted')"
          >
            mdi-check
          </v-icon>
          <v-icon
            small
            color="red"
            @click="setState(item, 'refused')"
          >
            mdi-close
          </v-icon>
        </template>
        </v-data-table>
      </v-col>
        <v-col cols="9">
          <h1 style="color:grey">Demandes fermes</h1>
          <hr/><br/>
          <v-data-table
          :headers="headers"
          :items="closedRequests"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
        </v-data-table>
      </v-col>
      </v-row>
    </v-container>
</template>

<script>

export default {
  name: 'Projects',
  data () {
    return {
      dialog: false,
      loading: false,
      projectId: '',
      options: {},
      itemSelect: ['En cours', 'Fini', 'En pause', 'Annul'],
      headers: [
        { text: 'Type', value: 'type', align: 'start' },
        { text: 'Etat', value: 'state' },
        { text: 'Photographer', value: 'photographer.name' },
        { text: 'Actions', value: 'actions', align: 'center' }
      ]
    }
  },
  computed: {
    closedRequests () {
      return this.$store.getters.getRequests.filter(request => ['accepted', 'refused'].includes(request.state))
    },
    openRequests () {
      return this.$store.getters.getRequests.filter(request => request.state === 'open')
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  mounted () {
    this.$store.dispatch('loadRequests')
  },
  methods: {
    setState (item, state) {
      this.$store.dispatch('setRequestState', { requestId: item._id, state: state }).then(
        (response) => {
          this.$store.dispatch('loadRequests')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    },
    deleteItem (item) {
      this.$store.dispatch('deleteRequest', item).then(
        (response) => {
          this.$store.dispatch('loadRequests')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    }
  }
}
</script>

