<template>
  <v-dialog
    v-model="dialog"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" dark v-bind="attrs" v-on="on"> Mettre  jour vos types de photographies </v-btn>
    </template>
    <v-card class="fill-height overflow-hidden" >
      <v-toolbar dark color="primary">
        <v-btn icon dark @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Mise  jour</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn dark text @click="handleForm"> Enregistrer </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="6">
            <h3 style="text-align:center; margin-bottom:25px;">Veuillez selectionnez les types de photographie que vous pouvez raliser</h3>
            <v-select
              v-model="value"
              :items="badges"
              item-text="name"
              item-value="_id"
              te
              attach
              chips
              label="Choisissez vos types"
              solo
              clearable
              multiple
            >
            <template v-slot:selection="{ attrs, item, select, selected }">
              <v-chip
                v-bind="attrs"
                :input-value="selected"
                @click="select"
              >
               {{ item.name }}
              </v-chip>
            </template>
          </v-select>
          </v-col>
          <v-col cols="8">
            <v-card>
              <v-tabs v-model="active_tab" grow>
                <v-tab style="justify-content: left !important;" v-for="item in badges" :key="item._id">
                  {{ item.name }}
                </v-tab>
                <v-tab-item v-for="item in badges" :key="item._id">
                  <v-card flat>
                    <v-card-text>
                      <v-row>
                        <v-col cols="9">
                          <h1>{{ item.name }}</h1>
                          <hr class="my-5" />
                          <p class="text-body-1">
                            {{ item.description }}
                          </p>
                        </v-col>
                        <v-col>
                          <v-img
                            :src="`https://picsum.photos/500/300?image=${ item.name.length * 5 + badges.indexOf(item.name) }`"
                            aspect-ratio="1"
                          ></v-img>
                        </v-col>
                      </v-row>
                    </v-card-text>
                  </v-card>
                </v-tab-item>
              </v-tabs>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
import Request from '../models/request'
export default {
  name: 'notification-box',
  data () {
    return {
      dialog: true,
      notifications: false,
      sound: true,
      widgets: false,
      active_tab: 0,
      items: [
        'Portrait',
        'Evenementiel',
        'Mode',
        'Nu',
        'Animalire',
        'Nature morte',
        'Culinaire',
        'Arienne',
        'Photojournalisme',
        'Mariage',
        "Oeuvres d'Art"
      ],
      value: []
    }
  },
  computed: {
    badges () {
      return this.$store.getters.getBadges
    }
  },
  mounted () {
    this.$store.dispatch('loadBadges')
  },
  methods: {
    handleForm () {
      if (this.value.length === 0) return
      const r = new Request()
      r.photographer = this.$store.state.auth.user.id
      r.badges = this.value
      r.accepted = false
      console.log(r)
      this.$store.dispatch('addRequest', r).then(
        (response) => {
          this.$store.dispatch('loadPhotographers')
          this.dialog = false
        },
        (error) => {
          this.loading = false
          this.message =
                error.response.data.message
        }
      )
    }
  }
}
</script>

