import Photographers from '@/views/admin/Photographers.vue'
import DashboardAdmin from '@/views/admin/DashboardAdmin.vue'
import ProjectAdd from '@/views/admin/ProjectAdd.vue'
import Requests from '@/views/admin/Requests.vue'
import Projects from '@/views/common/Projects.vue'
import Notifications from '@/views/common/Notifications.vue'
import UserProject from '@/views/common/UserProject.vue'
import UserProfile from '@/views/common/UserProfile.vue'

const routes = [
  {
    path: '/admin',
    name: 'admin',
    component: DashboardAdmin
  },
  {
    path: '/admin/demandes',
    name: 'demandes',
    component: Requests
  },
  {
    path: '/admin/photographes',
    name: 'photographes',
    component: Photographers
  },
  {
    path: '/admin/ajouter_projet',
    name: 'ajouter_projet',
    component: ProjectAdd
  },
  {
    path: '/admin/photographer/:id',
    name: 'Photographer',
    component: UserProfile
  },
  // Common
  {
    path: '/admin/projets',
    name: 'AdminProjects',
    component: Projects
  },
  {
    path: '/admin/notifications',
    name: 'AdminNotifications',
    component: Notifications
  },
  {
    path: '/admin/project/:id',
    name: 'UserProject',
    component: UserProject
  }
]

export default routes.map(route => {
  return { ...route, meta: { public: false } }
})
