<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="9">
        <h1>Photographes</h1>
        <hr />
        <br />
        <v-data-table
          :headers="headers"
          :items="photographers"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
          <template v-slot:item.actions="{ item }">
            <v-btn class="mx-2" small color="primary" :to="getRoute(item)">
              Plus d'informations
            </v-btn>
            <v-icon
              small
              @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'Projects',
  data () {
    return {
      loading: false,
      options: {},
      itemSelect: ['En cours', 'Fini', 'En pause', 'Annul'],
      headers: [
        { text: 'N', value: '_id', align: 'start' },
        { text: 'Nom', value: 'name' },
        { text: 'Pseudo', value: 'username' },
        { text: 'email', value: 'email' },
        { text: 'Ville', value: 'city' },
        { text: 'Actions', value: 'actions', align: 'right' }
      ]
    }
  },
  computed: {
    photographers () {
      return this.$store.getters.getPhotographers
    }
  },
  mounted () {
    this.$store.dispatch('loadPhotographers')
  },
  methods: {
    getRoute: function (item) {
      return '/photographer/' + item._id
    },
    deleteItem (item) {
      this.$store.dispatch('deletePhotographer', item).then(
        (response) => {
          this.$store.dispatch('loadPhotographers')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    }
  }
}
</script>

