<template>
  <div class="home pa-7">
    <v-row no-gutters>
      <v-col class="">
        <big-button
          class="mr-5 mb-5 elevation-5"
          text="Ajouter projet"
          color="success"
          icon="mdi-plus-circle"
          to="/admin/ajouter_projet"
        ></big-button>
        <v-btn
          class="mr-5 mb-5 elevation-5"
          @click="dialog = true"
          color="warning"
          dark
          style="min-height: 100px; min-width: 100px"
        >
          <v-col cols="12">
            <v-row
              width="100%"
              style="place-content: center; margin-bottom: 12px"
            >
              <v-icon x-large color="white"> mdi-pencil-circle </v-icon>
            </v-row>
            <v-row width="100%" style="place-content: center">
              <h3 style="font-weight: bold">Attribuer un photographe</h3>
            </v-row>
          </v-col>
        </v-btn>
        <v-dialog v-model="dialog" persistent max-width="600px">
          <v-form @submit.prevent="handleAttribution">
          <v-card>
            <v-card-title>
              <span class="headline">Attribuer un photographe</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-select
                  :items="projects"
                  v-model="projectId"
                  menu-props="auto"
                  item-text="name"
                  item-value="_id"
                  label="Choisir projet"
                  name="project"
                  prepend-icon="mdi-wrench"
                  single-line
                >
                  <template v-slot:item="{ item }">
                    <div>{{ item.name + " by " + item.company }}</div>
                  </template>
                </v-select>
                <v-select
                  :items="photographers"
                  menu-props="auto"
                  item-text="username"
                  return-object
                  label="Choisir photographe"
                  v-model="userId"
                  name="photographer"
                  prepend-icon="mdi-account"
                  single-line
                >
                  <template v-slot:item="{ item }">
                    <div>{{ item.name }}</div>
                  </template>
                </v-select>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog = false">
                Annuler
              </v-btn>
              <v-btn type="submit" color="blue darken-1" text @click="dialog = false">
                Confirmer
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </v-dialog>
        <big-button
          class="mr-5 mb-5 elevation-5"
          text="Page photographes"
          color="success"
          icon="mdi-account-supervisor-circle"
          to="/admin/photographes"
        ></big-button>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-data-table
          :headers="headers"
          :items="projects"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
        <template v-slot:item.actions="{ item }">
          <!-- <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon> -->
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
        <template v-slot:item.dates="{ item }">
          {{ item.dates.join(' / ') }}
        </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import BigButton from '../../components/BigButton.vue'

export default {
  name: 'Home',
  components: { BigButton },
  data () {
    return {
      dialog: false,
      loading: false,
      projectId: '',
      userId: '',
      options: {},
      headers: [
        { text: 'Nom du projet', value: 'name', align: 'start' },
        { text: 'Entreprise', value: 'company' },
        { text: 'Statut', value: 'status' },
        { text: 'Remuneration', value: 'remuneration' },
        { text: 'Dates', value: 'dates' },
        { text: 'Commentaires', value: 'comments' },
        { text: 'Actions', value: 'actions', align: 'center' }
      ]
    }
  },
  computed: {
    projects () {
      return this.$store.getters.getProjects
    },
    photographers () {
      return this.$store.getters.getPhotographers
    }
  },
  mounted () {
    this.$store.dispatch('loadProjects')
    this.$store.dispatch('loadUserProjects', this.$store.state.auth.user)
    this.$store.dispatch('loadPhotographers')
  },
  methods: {
    deleteItem (item) {
      this.$store.dispatch('deleteProject', item).then(
        (response) => {
          this.$store.dispatch('loadProjects')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    },
    handleAttribution () {
      this.$store.dispatch('setPhotographerToProject', { projectId: this.projectId, userId: this.userId }).then(
        (response) => {
          this.$store.dispatch('loadProjects')
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    }
  }
}
</script>
