import axios from 'axios'
import authHeader from './auth-header'

const API_URL = 'https://app.afroto.com/api/project/'

class ProjectService {
  getProjects () {
    return axios.get(API_URL, { headers: authHeader() })
  }

  getProject (project) {
    return axios.get(API_URL + project._id, { headers: authHeader() })
  }

  deleteProject (project) {
    return axios.delete(API_URL + project._id, { headers: authHeader() })
  }

  getProjectByPhotographer (data) {
    return axios.get(API_URL + 'getProjectByPhotographer/' + data.id, { headers: authHeader() })
  }

  validateProject (data) {
    console.log(data)
    return axios.post(API_URL + 'validateProject/' + data._id, {
      url: data.url
    }, { headers: authHeader() })
  }

  setPhotographer (data) {
    return axios.post(API_URL + 'setPhotographer', {
      project_id: data.projectId,
      photographer_id: data.userId
    },
    { headers: authHeader() })
  }

  postProject (project) {
    return axios
      .post(API_URL, {
        name: project.name,
        type: project.type,
        email: project.email,
        company: project.company,
        address: project.address,
        country: project.country,
        remuneration: project.remuneration,
        dates: project.dates,
        pictures: project.pictures,
        comments: project.comments,
        informations: project.informations,
        photographer: project.photographer
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }

  putProject (project) {
    return axios
      .put(API_URL + project._id, {
        name: project.name,
        type: project.type,
        email: project.email,
        company: project.company,
        address: project.address,
        country: project.country,
        remuneration: project.remuneration,
        dates: project.dates,
        pictures: project.pictures,
        comments: project.comments,
        informations: project.informations,
        photographer: project.photographer
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }
}

export default new ProjectService()
