import UserService from '../services/user.service'

export const user = {
  // namespaced: true,
  // state: initialState,
  state: {
    photographers: []
  },
  actions: {
    loadPhotographers ({ commit }) {
      UserService.getPhotographers().then(
        response => {
          commit('setPhotographers', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    updateProfile ({ commit }, item) {
      UserService.updatePhotographer(item).then(
        response => {},
        error => {
          this.content = error.response.data.message
        }
      )
    },
    addEquipement ({ commit }, item) {
      return UserService.addEquipement(item)
    },
    deleteEquipement ({ commit }, item) {
      return UserService.deleteEquipement(item)
    },
    deletePhotographer ({ commit }, item) {
      return UserService.deletePhotographer(item)
    }
  },
  mutations: {
    setPhotographers (state, photographers) {
      state.photographers = photographers
    }
  },
  getters: {
    getPhotographers (state) {
      return state.photographers
    },
    getPhotographer: (state) => (id) => {
      return state.photographers.find(photographer => photographer._id === id)
    }
  }
}
