<template>

    <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="9">
          <h1>Projets</h1>
          <hr/><br/>
          <v-data-table
          :headers="headers"
          :items="myProjects"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
        <!-- <template v-slot:[`item.status`]>
          <v-select
          :items="itemSelect"
          menu-props="auto"
          item-text="item"
          label="Select"
          chips
          single-line
        >
          <template #selection="{ item }">
            <v-chip text-color="white" close :color="getColor(item)">{{item}}</v-chip>
          </template>
        </v-select>
        </template> -->
        <template v-slot:item.actions="{ item }">
            <v-btn class="mx-2" small color="primary" :to="getRoute(item)">
              Plus d'informations
            </v-btn>
          </template>
        </v-data-table>
      </v-col>
      </v-row>
    </v-container>
</template>

<script>

export default {
  name: 'Projects',
  data () {
    return {
      dialog: false,
      loading: false,
      projectId: '',
      options: {},
      itemSelect: ['En cours', 'Fini', 'En pause', 'Annul'],
      headers: [
        { text: 'Nom du projet', value: 'name', align: 'start' },
        { text: 'Entreprise', value: 'company' },
        { text: 'Statut', value: 'status' },
        { text: 'Remuneration', value: 'remuneration' },
        { text: 'Dates', value: 'dates' },
        { text: 'Commentaires', value: 'comments' },
        { text: 'Actions', value: 'actions', align: 'center' }
      ]
    }
  },
  computed: {
    myProjects () {
      return this.$store.getters.getUserProjects
    },
    photographers () {
      return this.$store.getters.getPhotographers
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  mounted () {
    this.$store.dispatch('loadProjects')
    this.$store.dispatch('loadUserProjects', this.$store.state.auth.user)
    this.$store.dispatch('loadPhotographers')
  },
  methods: {
    getColor: function (item) {
      if (item === 'En cours') {
        return 'blue'
      } else if (item === 'Fini') {
        return 'red'
      } else if (item === 'En pause') {
        return 'orange'
      } else if (item === 'Annul') {
        return 'black'
      }
    },
    getRoute: function (item) {
      return '/project/' + item._id
    }
  }
}

</script>

