
import DashboardPhotographer from '@/views/photographer/DashboardPhotographer.vue'
import Calendar from '@/views/photographer/Calendar.vue'

import UserProfile from '@/views/common/UserProfile.vue'
import Projects from '@/views/common/Projects.vue'
import Notifications from '@/views/common/Notifications.vue'

const routes = [

  {
    path: '/home',
    name: 'DashboardPhotographer',
    component: DashboardPhotographer
  },
  {
    path: '/profil',
    name: 'Profile',
    component: UserProfile
  },
  {
    path: '/calendrier',
    name: 'Calendar',
    component: Calendar
  },
  {
    path: '/projets',
    name: 'Projects',
    component: Projects
  },
  {
    path: '/notifications',
    name: 'Notifications',
    component: Notifications
  }
]

export default routes.map(route => {
  return { ...route, meta: { public: false } }
})
