import axios from 'axios'
import authHeader from './auth-header'

const API_URL = 'https://app.afroto.com/api/notification/'

class NotificationService {
  getNotifications () {
    return axios.get(API_URL, { headers: authHeader() })
  }

  getNotification (notification) {
    return axios.get(API_URL + notification._id, { headers: authHeader() })
  }

  deleteNotification (notification) {
    return axios.delete(API_URL + notification._id, { headers: authHeader() })
  }

  getNotificationByPhotographer (data) {
    return axios.get(API_URL + 'getNotificationByPhotographer/' + data.id, { headers: authHeader() })
  }

  setPhotographer (data) {
    return axios.post(API_URL + 'setPhotographer', {
      notification_id: data.notificationId,
      photographer_id: data.userId
    },
    { headers: authHeader() })
  }

  postNotification (notification) {
    return axios
      .post(API_URL, {
        message: notification.message,
        deadline: notification.deadline,
        importance: notification.importance,
        seen: notification.seen
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }

  putNotification (notification) {
    return axios
      .put(API_URL + notification._id, {
        name: notification.name,
        type: notification.type,
        email: notification.email,
        seen: notification.seen
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }
}

export default new NotificationService()
