import BadgeService from '../services/badge.service'

export const badge = {
  // namespaced: true,
  // state: initialState,
  state: {
    badges: [],
    userBadges: []
  },
  actions: {
    loadBadges ({ commit }) {
      BadgeService.getBadges().then(
        response => {
          commit('setBadges', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    loadUserBadges ({ commit }, item) {
      BadgeService.getBadgeByPhotographer(item).then(
        response => {
          commit('setUserBadges', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    updateBadge ({ dispatch }, badge) {
      BadgeService.putBadges(badge).then(
        response => {
          dispatch('loadBadges')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    deleteBadge ({ dispatch }, badge) {
      BadgeService.deleteBadge(badge).then(
        response => {
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    addBadge ({ dispatch }, badge) {
      BadgeService.postBadge(badge).then(
        response => {
          dispatch('loadBadges')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    }
  },
  mutations: {
    setBadges (state, badges) {
      state.badges = badges
    },
    setUserBadges (state, badges) {
      state.userBadges = badges
    }
  },
  getters: {
    getBadges (state) {
      return state.badges
    },
    getUserBadges (state) {
      return state.userBadges
    }
  }
}
