import axios from 'axios'
import authHeader from './auth-header'

const API_URL = 'https://app.afroto.com/api/badge/'

class BadgeService {
  getBadges () {
    return axios.get(API_URL, { headers: authHeader() })
  }

  getBadge (badge) {
    return axios.get(API_URL + badge._id, { headers: authHeader() })
  }

  deleteBadge (badge) {
    return axios.delete(API_URL + badge._id, { headers: authHeader() })
  }

  setPhotographer (data) {
    return axios.post(API_URL + 'setPhotographer', {
      badge_id: data.badgeId,
      photographer_id: data.userId
    },
    { headers: authHeader() })
  }

  postBadge (badge) {
    return axios
      .post(API_URL, {
        message: badge.message,
        deadline: badge.deadline,
        importance: badge.importance,
        seen: badge.seen
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }

  putBadge (badge) {
    return axios
      .put(API_URL + badge._id, {
        name: badge.name,
        type: badge.type,
        email: badge.email,
        seen: badge.seen
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }
}

export default new BadgeService()
