<template>
  <v-alert
      :color="color"
      dark
      icon="mdi-vuetify"
      prominent
      dismissible
      v-model="alert"
      @input="deleteAlert"
    >
      <v-row align="center">
        <v-col class="grow">
          {{text}}
        </v-col>
      </v-row>
    </v-alert>
</template>

<script>
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'notification-box',
  data: () => ({
    alert: true
  }),
  props: {
    text: String,
    icon: String,
    color: String,
    id: String
  },
  methods: {
    deleteAlert () {
      this.$store.dispatch('deleteNotification', { _id: this.id }).then(
        (response) => {
          this.$store.dispatch('loadUserNotifications', this.$store.state.auth.user)
        },
        (error) => {
          alert(error.response.data.message)
        }
      )
    }
  }
}
</script>

<style scoped>

.v-snack__wrapper {
  min-width: 75%;
}

</style>
