<template>
  <div class="home pa-7">
    <v-row >
      <v-col>
        <v-card dark color="primary">
          <v-card-title>Projets disponibles :</v-card-title>
          <v-card-text>
            <h1>{{this.$store.getters.getUserProjects.filter(i => i.status !== 'Fini').length}}</h1>
            <br />
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card dark color="primary">
          <v-card-title>Projets termins :</v-card-title>
          <v-card-text>
            <h1>{{this.$store.getters.getUserProjects.filter(i => i.status === 'Fini').length}}</h1>
            <br />
          </v-card-text>
        </v-card>
      </v-col>
            <v-col>
        <v-card dark color="primary">
          <v-card-title>Notifications en attente :</v-card-title>
          <v-card-text>
            <h1>{{this.$store.state.notification.userNotifications.filter(i => !i.seen).length}}</h1>
            <br />
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
<h1>Projets</h1>
          <hr/><br/>
          <v-data-table
          :headers="headers"
          :items="myProjects"
          :options.sync="options"
          :server-items-length="10"
          :loading="loading"
          class="elevation-3"
        >
        <template v-slot:item.status="{ item }">
            <v-chip v-if="item.status" text-color="white" :color="getColor(item.status)">{{item.status}}</v-chip>
        </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>

export default {
  name: 'Home',
  data () {
    return {
      loading: false,
      options: {},
      headers: [
        { text: 'Nom du projet', value: 'name', align: 'start' },
        { text: 'Entreprise', value: 'company' },
        { text: 'Statut', value: 'status' },
        { text: 'Remuneration', value: 'remuneration' },
        { text: 'Dates', value: 'dates' },
        { text: 'Commentaires', value: 'comments' }
      ]
    }
  },
  methods: {
    getColor: function (color) {
      if (color === 'En cours') {
        return 'blue'
      } else if (color === 'Fini') {
        return 'red'
      } else if (color === 'En pause') {
        return 'orange'
      } else if (color === 'Annul') {
        return 'black'
      }
    }
  },
  computed: {
    projects () {
      return this.$store.getters.getProjects
    },
    myProjects () {
      return this.$store.getters.getUserProjects
    },
    photographers () {
      return this.$store.getters.getPhotographers
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    }
  },
  mounted () {
    this.$store.dispatch('loadProjects')
    this.$store.dispatch('loadUserProjects', this.$store.state.auth.user)
    this.$store.dispatch('loadPhotographers')
  }
}
</script>

