import RequestService from '../services/request.service'

export const request = {
  // namespaced: true,
  // state: initialState,
  state: {
    requests: [],
    userRequests: []
  },
  actions: {
    loadRequests ({ commit }) {
      RequestService.getRequests().then(
        response => {
          commit('setRequests', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    loadUserRequests ({ commit }, item) {
      RequestService.getRequestByPhotographer(item).then(
        response => {
          commit('setUserRequests', response.data.data)
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    updateRequest ({ dispatch }, request) {
      RequestService.putRequests(request).then(
        response => {
          dispatch('loadRequests')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    deleteRequest ({ dispatch }, request) {
      RequestService.deleteRequest(request).then(
        response => {
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    addRequest ({ dispatch }, request) {
      RequestService.postRequest(request).then(
        response => {
          dispatch('loadRequests')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    },
    setRequestState ({ dispatch }, requestId, state) {
      RequestService.setRequestState(requestId, state).then(
        response => {
          dispatch('loadRequests')
        },
        error => {
          this.content = error.response.data.message
        }
      )
    }
  },
  mutations: {
    setRequests (state, requests) {
      state.requests = requests
    },
    setUserRequests (state, requests) {
      state.userRequests = requests
    }
  },
  getters: {
    getRequests (state) {
      return state.requests
    },
    getUserRequests (state) {
      return state.userRequests
    }
  }
}
