import axios from 'axios'
import authHeader from './auth-header'

const API_URL = 'https://app.afroto.com/api/request/'

class RequestService {
  getRequests () {
    return axios.get(API_URL, { headers: authHeader() })
  }

  getRequest (request) {
    return axios.get(API_URL + request._id, { headers: authHeader() })
  }

  deleteRequest (request) {
    return axios.delete(API_URL + request._id, { headers: authHeader() })
  }

  getRequestByPhotographer (data) {
    return axios.get(API_URL + 'getRequestByPhotographer/' + data.id, { headers: authHeader() })
  }

  postRequest (request) {
    return axios
      .post(API_URL, {
        photographer: request.photographer,
        state: request.state,
        badges: request.badges
        // type: request.type,
        // url: request.url,
        // description: request.description
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }

  setRequestState (data) {
    return axios.post(API_URL + 'setRequestState', {
      request_id: data.requestId,
      state: data.state
    },
    { headers: authHeader() })
  }

  putRequest (request) {
    return axios
      .put(API_URL + request._id, {
        type: request.type,
        url: request.url,
        description: request.description,
        state: request.state,
        badges: request.badges,
        photographer: request.photographer
      }, { headers: authHeader() })
      .then(response => {
        return response.data
      })
  }
}

export default new RequestService()
