<template>
  <v-app>
    <v-app-bar app clipped-left color="primary" dark>
      <div class="d-flex align-center">
        <router-link to="/"><h1 style="color:white">Afroto</h1></router-link>
      </div>
      <v-spacer></v-spacer>
      <router-link to="/notifications">
      <v-badge
        :content="getNbNotif()"
        :value="getNbNotif()"
        color="error"
        overlap
      >
        <v-btn
          class="white--text"
          color="blue lighten-1"
          fab
          small
          @mouseover="iconNotif = 'mdi-bell'"
          @mouseout="iconNotif = 'mdi-bell-outline'"
        >
          <v-icon>{{ iconNotif }}</v-icon>
        </v-btn>
      </v-badge>
      </router-link>
      <div v-if="!this.loggedIn">
        <v-btn text to="/register">Inscription</v-btn>
        <v-btn text to="/login">Connexion</v-btn>
      </div>
      <div v-if="this.loggedIn">
        <v-btn class="ml-5" icon @click.prevent="logOut">
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </div>
    </v-app-bar>
    <v-navigation-drawer permanent expand-on-hover clipped app>
      <v-list nav dense>
        <v-list-item
          v-for="item in sideBarItems"
          :key="item.link"
          :to="item.link"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-title>{{ item.text }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  components: {},
  data: () => ({
    iconNotif: 'mdi-bell-outline',
    isAdmin: false,
    items: null,
    nbNotif: 5,
    show: false,
    itemsPhoto: [
      { icon: 'mdi-home-outline', text: 'Accueil', link: '/home' },
      { icon: 'mdi-inbox-full-outline', text: 'Mes Projets', link: '/projets' },
      // { icon: 'mdi-inbox-outline', text: 'Projet Example', link: '/projet' },
      { icon: 'mdi-calendar-outline', text: 'Calendrier', link: '/calendrier' },
      { icon: 'mdi-bell-outline', text: 'Notifications', link: '/notifications' },
      { icon: 'mdi-account-outline', text: 'Mon Profil', link: '/profil' }
    ],
    itemsAdmin: [
      { icon: 'mdi-home-outline', text: 'Accueil', link: '/admin' },
      { icon: 'mdi-inbox-full-outline', text: 'Projets', link: '/admin/projets' },
      { icon: 'mdi-account-multiple-outline', text: 'Photographes', link: '/admin/photographes' },
      { icon: 'mdi-link', text: 'Demandes', link: '/admin/demandes' }
      // { icon: 'mdi-bell-outline', text: 'Notifications', link: '/notifications' }
    ]
  }),

  created: function () {
    if (!this.loggedIn) {
      this.item = []
      return
    }
    if (this.currentUser.roles.includes('ROLE_ADMIN')) {
      this.items = this.itemsAdmin
    } else {
      this.items = this.itemsPhoto
    }
  },
  computed: {
    sideBarItems () {
      if (!this.loggedIn) {
        return []
      }
      if (this.currentUser.roles.includes('ROLE_ADMIN')) {
        return this.itemsAdmin
      } else {
        return this.itemsPhoto
      }
    },
    loggedIn () {
      return this.$store.state.auth.status.loggedIn
    },
    currentUser () {
      return this.$store.state.auth.user
    }
  },
  methods: {
    logOut () {
      this.$store.dispatch('auth/logout')
      this.$router.push('/login')
    },
    getNbNotif () {
      return this.$store.state.notification.userNotifications.filter(i => !i.seen).length
    }
  }
}
</script>
